.input:focus {
    outline: none;
}

.input {
    width: 372px;
    height: 36px;
    align-items: flex-start;
    flex-shrink: 0;
    gap: 5px;
    border-radius: 5px;
}

.inputWrapper {
    display: flex;
    flex-direction: column-reverse;
}

.superInput {
    border: 1px solid #D1D1D1;
}

.errorInput {
    border: 1px solid #CC1439;
    outline: none;
}

.errorInput:focus {
    outline: none;
}

.error {
    color: #CC1439;
    font-family: Montserrat;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}
