* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
} 


.App {
    --header_height: 60px;
    font-family: Montserrat;
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.hw {
    width: 1260px;
    border: 1px solid #D9D9D9;
}

.hwTitle {
    color: #000;
    font-family: Montserrat;
    font-size: 22px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    margin-left: 27px;
}