.button {
    color: #fff;
    border: transparent;
    border-radius: 3px;
    cursor: pointer;
    display: inline-flex;
    padding: 5px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
}

.disabled {
    opacity: 0.5;
    background-color: #004D99;
    color: #002E5C;
}

.secondary {
    color: #0066CC;
    background-color: #fff;
    border: 1px solid #0066CC; 
}

.default {
    background-color: #0066CC;
}

.red {
    background-color: #CC1439;
}

.default:hover {
    background: #0080FF;
}

.red:hover {
    background: #FF1A47;
}

.secondary:hover {
    background: #E5F0FA;
}


.default:active {
    background: #0059B2;
}

.secondary:active {
    background: #C9E5FF;
}

.red:active {
    background: #B21232;
}

.button:disabled {
    cursor: default;
}
